import { Component, Inject, OnInit } from '@angular/core';
import { FormControl, FormGroup } from '@angular/forms';
import { fuseAnimations } from '@fuse/animations';
import { JedsMenuService } from '@shared/service/app/jeds-menu.service';
import { ICliente } from '../../models/cliente';
import { firstValueFrom, map, Observable, startWith, Subject, takeUntil } from 'rxjs';
import { ClientesService } from '../../service/clientes.service';
import { ILocalidad } from '@shared/models/localidad';
import { EmpresaService } from '@shared/service/app/empresa.service';

@Component({
  selector: 'app-filter-menu-clientes',
  templateUrl: './filter-menu.component.html',
  styleUrls: ['./filter-menu.component.scss'],
  animations: fuseAnimations
})
export class FilterMenuComponent implements OnInit {
  public formFilters: FormGroup;
  clientes: ICliente[] = [];
  localidades: ILocalidad[] = [];
  filteredClientes: Observable<ICliente[]>;
  filteredCuit: Observable<ICliente[]>;
  filteredLocalidades: Observable<ILocalidad[]>;
  color_primario: string;
  color_secundario: string;
  default_color: string = 'bg-primary';
  private _unsubscribeAll: Subject<any> = new Subject<any>();
  constructor(
    @Inject('MENU_DATA') public data: any, 
    private _menuService: JedsMenuService,
    private _clientesService: ClientesService,
    private _empresaService: EmpresaService
  ) { }

  ngOnInit(): void {
    /**
    * Formulario de filtros
    */
    this.formFilters = new FormGroup({
      razonSocial: new FormControl(''),
      Cuit: new FormControl(''),
      localidad_descripcion: new FormControl('')
    });
    this.getClientes();
    this.getLocalidades();
    // Subscribe to empresa data
    this._empresaService.empresa$
    .pipe(takeUntil(this._unsubscribeAll))
    .subscribe(empresa => {
        this.color_primario = empresa.color_primario;
        this.color_secundario = empresa.color_secundario;
    });

    // Load empresa data
    this._empresaService.getEmpresa();
    this.formFilters.patchValue(this.data.filter);
  }

  resetFilters() {
    this.formFilters.reset({
      razonSocial: '',
      Cuit: '',
      localidad_descripcion: ''
    });
  }

  /**
    * Esta funcion es para listar los clientes en el filtro de grilla.
    */
  async getClientes() {
    const Clientes: any = await firstValueFrom(this._clientesService.getClientes());
    this.clientes = Clientes.list;
    this.filteredClientes = this.formFilters.controls['razonSocial'].valueChanges.pipe(
      startWith(''),
      map(value => this._filterCliente(value))
    )
    this.filteredCuit = this.formFilters.controls['Cuit'].valueChanges.pipe(
      startWith(''),
      map(value => this._filterCliente(value))
    )
  }

  /**
    * Esta funcion es para filtrar los clientes según el input que se haya ingresado en el input de razon social y cuit.
    * @param value 
    * @returns 
    */
  private _filterCliente(value: string) {
    const filterValue = value.toLowerCase();
    return this.clientes.filter(cliente =>
      cliente.razonSocial.toLowerCase().includes(filterValue) ||
      cliente.Cuit.toLowerCase().includes(filterValue)
    );
  }

  /**
    * Esta funcion es para listar las localidades en el filtro de grilla.
    */
  async getLocalidades() {
    const Localidades = await firstValueFrom(this._clientesService.getLocalidades())
    this.localidades = Localidades.list
    this.filteredLocalidades = this.formFilters.controls['localidad_descripcion'].valueChanges.pipe(
      startWith(''),
      map(value => this._filterLocalidad(value))
    )
  }

  /**
    * Esta funcion es para filtrar las localidades según el input que se haya ingresado en el input de localidad_descripcion.
    * @param value 
    * @returns 
    */
  private _filterLocalidad(value: string) {
    const filterValue = value.toLowerCase();
    return this.localidades.filter(localidad =>
      localidad.descripcion.toLowerCase().includes(filterValue) 
    );
  }

  onCloseMenu(data: any = null) {
    this._menuService.close(data);
  }
}